.app{
    width: 100vw;
    height: 100vh;
    background: #000;
    position: relative;
    color: #fff;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    overflow: hidden;
    position: relative;
}

.error-message{
    text-align: center;
    position: absolute;
    bottom: 3%;
    font-size: 2vmax;
}

@media screen and (min-width:600px) and (orientation:landscape){
    .app{
        flex-direction: row;
        
    }
}