.weather {
  text-align: center;
  font-size: 2.5vmax;
  height: auto;
  display: flex;
  padding: 10px;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  cursor: pointer;
  transition: 1s ease all;
}

.weather:hover{
  background: #222;
}


.weather * {
  margin: 3% 0;
}

.icon {
  width: 40%;
  margin-bottom: 0;
}
